import React, { useState } from "react";
import Chart from "react-apexcharts";

import ShowRegressionString from "./ShowRegressionString";

import Container from "react-bootstrap/Container";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";

function ScatterChartBetaCompany(props) {
    const [company, setCompany] = useState("ROSN");

    let companies = [
        "SBER",
        "YNDX",
        "VTBR",
        "SNGSP",
        "SBERP",
        "ROSN",
        "NLMK",
        "MTSS",
        "MGNT",
        "MFON",
        "GMKN",
        "GAZP",
        "AFLT",
        "IMOEX"
    ];


    const round = (value, decimals) => {
        return Number(Math.round(value + "e" + decimals) + "e-" + decimals);
    };

    let imoex = [];
    let selectedstock = [];
    let imoexLine = [];
    let companyLine = [];

    props.data.forEach(item => {
        if (item.node.TICKER === "IMOEX") {
            let result = round(
                ((item.node.CLOSE - item.node.OPEN) * 100) / item.node.OPEN,
                2
            );
            if (!isNaN(result)) {
                imoex.push({
                    DATE: item.node.DATE,
                    PERIODRETURN: result
                });
            }
            imoexLine.push({ date: item.node.DATE, value: parseFloat(item.node.CLOSE) });
        }

        if (item.node.TICKER === company) {
            let result = round(
                ((item.node.CLOSE - item.node.OPEN) * 100) / item.node.OPEN,
                2
            );
            if (!isNaN(result)) {
                selectedstock.push({
                    DATE: item.node.DATE,
                    PERIODRETURN: result
                });
            }

            companyLine.push({ date: item.node.DATE, value: parseFloat(item.node.CLOSE) });
        }
    });

    let minLine2 = companyLine[0].value;
    let minLine1 = imoexLine.filter(item => item.date === companyLine[0].date)[0].value;
 
    let newcompanyLine = companyLine.map((item) => {
        let newItem = {...item};
        newItem.value = round(
            item.value * 100 / minLine2,
            2
        );
        return newItem
    });

    let newimoexLine = companyLine.map((item) => {
        let newMoexItem = imoexLine.filter(moexitem => moexitem.date === item.date)[0];
        let newItem = { ...newMoexItem };
        newItem.value =
            round(
                newMoexItem.value * 100 / minLine1,
                2
            );
        return newItem
    });

    let datesLine = companyLine.map((item) => {

        if (!/^(\d){8}$/.test(item.date)) return "invalid date";
        var y = item.date.substr(0, 4),
            m = item.date.substr(4, 2),
            d = item.date.substr(6, 2);
        let date = new Date(y, m, d).getTime(); //getTime();
     //   console.log(date);
        return date
    });

    let scatterdata = [];
    let arrayOfarrays = []; //["Date", "IMOEX", company]


    selectedstock.length > 0 &&
        selectedstock.forEach(item => {
            let found = imoex.find(function (element) {
                return element.DATE === item.DATE;
            });
            let imoexPeriodReturn = found.PERIODRETURN;

            scatterdata.push({
                horizontal: imoexPeriodReturn,
                vertical: item.PERIODRETURNx
            });
            arrayOfarrays.push([imoexPeriodReturn, item.PERIODRETURN]); //item.DATE,
        });

    //    console.log(props);

    let areaseries = [{
        name: 'Индекс',
        data: newimoexLine.map(item => { return item.value })
    }, {
        name: 'Актив',
        data: newcompanyLine.map(item => { return item.value })
    }];

    let areaoptions = {
        chart: {
            type: 'area',
            toolbar: {
                show: false,
            }
        },
        dataLabels: {
            enabled: false
        },
        stroke: {
            curve: 'smooth'
        },
        xaxis: {
            type: 'datetime',
            categories: datesLine
        },
        tooltip: {
            x: {
                format: 'dd/MM/yy'
            },
        },
    };

    let scatteroptions = {
        chart: {
            // height: 350,
            type: 'scatter',
            toolbar: {
                show: false,
            },
            zoom: {
                enabled: true,
                type: 'xy'
            }
        },
        xaxis: {
            title: {
                text: 'Доходность фондового индекса за месяц',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-yaxis-title',
                }
            },
            tickAmount: 16,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            },
            min: -40,
            max: 40
        },
        yaxis: {
            title: {
                text: 'Доходность актива за месяц',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-yaxis-title',
                }
            },
            tickAmount: 16,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            },
            min: -40,
            max: 40
        }
    };


    let scatterseries = [
        {
            name: "Доходность актива за период",
            data: arrayOfarrays
            //    [...new Array(49)].map(() => { let randomReturn = Math.round(Math.random() * 300 - 140) / 10; return [randomReturn, randomReturn * (1 - Math.random())] })
        }
    ];

    // let indexData = [100];
    // let assetData = [100];
    // scatterseries[0].data.map((item, index) => {
    //     indexData[index + 1] = Math.round(indexData[index] * (1 + item[0] / 100));
    //     assetData[index + 1] = Math.round(assetData[index] * (1 + item[1] / 100));
    //     return null
    // })

    // console.log(indexData);
    // console.log(assetData);


    //  console.log(dataforlinechart);
    //   console.log(arrayOfarrays);

    return <Container className="mt-3" >
        <ButtonGroup size="sm">
            {companies.map((item, index) => (
                <Button
                    variant="outline-secondary"
                    key={index}
                    onClick={() => setCompany(item)}
                //      style={{ margin: "5px", background: "white" }}
                >
                    {item}
                </Button>
            ))}
        </ButtonGroup>
        <ShowRegressionString arrayOfarrays={arrayOfarrays} />
        <Chart options={scatteroptions} series={scatterseries} type="scatter" />
        <Chart options={areaoptions} series={areaseries} type="area" />
    </Container>

}

export default ScatterChartBetaCompany;
